.Menu {
  width: 100%;
  padding: 0.3rem 0 0rem 0;
  display: flex;
  flex-shrink: 0;
}

.Menu .Menu_Nav {
  width: 2.5rem;
  height: 13.5rem;
  background: #f8f8f8;
  display: flex;
  flex-direction: column;
  flex-shrink: 0;
  overflow-y: auto;
}

.Menu .Menu_Nav .navList {
  padding: 0.5rem 0.1rem;
  flex-shrink: 0;
  display: flex;
  justify-content: center;
  color: #666;
  font-size: 0.3rem;
}

.Menu .Menu_Nav .NavActive {
  background: #fff;
}

.Menu .Menu_List {
  flex: 1;
  overflow-y: auto;
}

.Menu .Menu_List .list_Item {
  width: 100%;
}

.Menu .Menu_List .list_Item .list_Item_tit {
  width: 100%;
  padding: 0.3rem;
  box-sizing: border-box;
}

.Menu .Menu_List .list_Item .list_Item_detail {
  width: 100%;
  display: flex;
  padding: 0.2rem;
  box-sizing: border-box;
}

.Menu .Menu_List .list_Item .list_Item_detail .list_Item_img {
  width: 2.5rem;
}

.Menu .Menu_List .list_Item .list_Item_detail .list_Item_img > img {
  width: 100%;
}

.Menu .Menu_List .list_Item .list_Item_detail .list_Item_Msg {
  flex: 1;
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
  padding: 0 0.2rem;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.Menu .Menu_List .list_Item .list_Item_detail .list_Item_Msg h3 {
  font-size: 0.4rem;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.Menu .Menu_List .list_Item .list_Item_detail .list_Item_Msg span {
  font-size: 0.3rem;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  color: #999;
}

.Menu .Menu_List .list_Item .list_Item_detail .list_Item_Msg .list_Item_pic {
  display: flex;
  width: 100%;
  justify-content: space-between;
}

.Menu .Menu_List .list_Item .list_Item_detail .list_Item_Msg .list_Item_pic > p {
  color: #ff5339;
}

.Menu .Menu_List .list_Item .list_Item_detail .list_Item_Msg .list_Item_pic > p i {
  font-size: 0.5rem;
}

.Menu .Menu_List .list_Item .list_Item_detail .list_Item_Msg .list_Item_pic .rec_item_btns {
  display: flex;
  justify-content: center;
}

.Menu .Menu_List .list_Item .list_Item_detail .list_Item_Msg .list_Item_pic .rec_item_btns > p {
  padding: 0 0.5rem;
}

.Menu .Menu_List .list_Item .list_Item_detail .list_Item_Msg .list_Item_pic .rec_item_btns .btn {
  width: 0.6rem;
  height: 0.6rem;
  border-radius: 50%;
  background: #2395ff;
  text-align: center;
  line-height: 0.6rem;
}

.Menu .Menu_List .list_Item .list_Item_detail .list_Item_Msg .list_Item_pic .rec_item_btns .btn span {
  color: #fff;
  font-size: .5rem;
  line-height: 0.6rem;
  z-index: 999;
}
